.year-title {
  font-size: 18px !important;
}

.actions {
  flex: 1;
  display: flex;
  flex-direction: column;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
}

header {
  padding: 0 !important;
}

.check-header {
  height: 45px;
  background: #600000;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.add-courses-header {
  height: 45px;
  background: #600000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.add-courses-header span {
  font-size: 17px !important;
  font-weight: 200;
}
.add-courses-header .fa-info-circle {
  font-size: 20px !important;
  color: #fff;
  margin: 0 10px;
  margin-left: auto;
}
.add-courses-header .fa-info-circle:hover {
  color: #ccc;
}
.add-courses-header .fa-square-full {
  visibility: hidden;
  font-size: 20px !important;
  margin-right: auto;
}

.form-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.2);
  animation: fadein 0.3s;
}

.form-container .box {
  display: flex;
  flex-direction: column;
  background: #fff;
  width: 400px;
}

.form-container .box .check-header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px !important;
  font-weight: 200;
}

.form-container .box .add-container {
  display: flex;
}

.form-container .box .add-course {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 10px;
  color: #000;
  justify-content: center;
  align-items: center;
}

.form-container .box .add-course input {
  width: 50%;
  padding: 5px;
  font-size: 16px !important;
  font-weight: 200;
}

.form-container .box .add-course input:focus {
  outline: 0;
}

.form-container .box .add-semester {
  flex: 1;
  border-right: 1px solid #ccc;
  color: #000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form-container .box .add-summer {
  display: flex;
  flex-direction: row;
}

.button {
  background: #600000;
  padding: 10px;
  margin: 5px 0;
  color: #fff;
  border-radius: 6px;
  font-size: 16px !important;
  font-weight: 200;
  cursor: pointer;
}

.modal-buttons {
  width: 50%;
}

.button:focus {
  outline: 0;
}

.button:hover {
  background: #800000;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes grow {
  from {
    width: 10px;
  }
  to {
    width: 100px;
  }
}
span > .fa-plus {
  font-size: 18px !important;
  cursor: pointer;
  transition: all 0.2s;
}

span > .fa-plus:hover {
  color: #f0f0f0;
}

.year-title-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 4px;
  background: #eee;
  color: #aaa;
  height: 40px;
  font-weight: 100;
  height: auto;
}

.summer {
  display: none;
}

.summer.visible {
  display: flex;
}

.add-summer-button {
  width: auto;
  height: 10px;
  color: #333;
  margin-left: 10px;
  cursor: pointer;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.add-summer-button:hover {
  text-decoration: underline #666;
}

.check-container {
  height: 100%;
}

.check-courses-btn {
  width: 100%;
  height: 40px;
  background: #600000;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px !important;
  font-weight: 200;
  transition: all 0.2s;
}

.check-courses-btn:hover {
  cursor: pointer;
  background: #800000;
}

.transfer {
  background: #ccc;
}

[data-id=newCard] {
  background: #286DA8 !important;
}

[data-coursetype=ucc] {
  background: #CD5360;
}

[data-coursetype=pre-engr] {
  background: #CD5360;
}

[data-coursetype=engr] {
  background: #CD5360;
}

.outlined-pre {
  background: #238496;
  transition: background-color 0.5s;
}

.outlined-co {
  background: #8A6DA8;
  transition: background-color 0.5s;
}

.check-output {
  padding: 15px;
}

.description-title {
  font-size: 15px !important;
  font-weight: 600;
}

.description-credit {
  margin: 10px 0 0 0;
  font-weight: 600;
}

.description-body {
  text-align: justify;
  margin: 10px 0;
}

.description-footer {
  text-align: justify;
}

.check-instructions {
  color: #aaa;
}

.check-body {
  margin: 10px 0;
  padding: 10px;
  font-size: 20px !important;
  text-align: left;
  font-weight: 300;
}

.fa-info-circle {
  margin: 0 5px;
  cursor: pointer;
  transition: all 0.5s;
}

.fa-info-circle:hover {
  color: #333;
}

.info {
  color: #fff;
  font-size: 20px !important;
  margin: 0 10px;
}
.info:hover {
  color: #ccc;
}

.links {
  display: flex;
  flex-direction: column;
  margin: 10px 0;
  text-decoration: none;
}

a {
  background-color: #600000;
  color: white;
  padding: 10px 10px;
  text-decoration: none;
  text-transform: uppercase;
  transition: background-color 0.3s;
  margin: 10px 0;
  text-align: center;
}
a:hover {
  background-color: #800000;
  cursor: pointer;
}

.shepherd-footer li {
  list-style-type: none;
}

.hopscotch-content {
  font-size: 13px !important;
}

